import Performance, { Memory } from './performance.type';
import Log from './log.type';
import Resource from './resource.type';
import Event from './event.type';
import PV from './pv.type';
import PD from './pd.type';

export interface Region {
    lng: string,				// longitude
    lat: string					// latitude
}

export enum BridgeType {
    NONE = 'NONE'
}

export interface Bridge {
    type: BridgeType
    // ....
}

export interface UA {
    ua: string,					// us string
    browser: string,			// 
    os: string,					// 
    device?: string,			// 
    platform?: string
}

export enum ThirdPartyType {
    NONE,						// TODO: to string
    // NONE = "NONE",
}

export interface ThirdParty {
    type: ThirdPartyType		//  sdk 
    version?: string			//  sdk 
    apiVersion?: string,		// [OPTIONAL] cat 
}

export interface Network {
    online?: boolean,			// [OPTIONAL] 
    isp?: string,				// [OPTIONAL] 
    connection?: string			// [OPTIONAL] 
}

export default interface Message {
    // runtime
    "@timestamp": string,		// ES  UTC timestamp
    env: string,				// 
    project: string,			// 
    thirdparty: ThirdParty,		//  sdk
    auto: boolean,				// (falsetrue)
    userAgent: UA,						// ua 
    os?: string,				// [] 
    device?: string,			// [] 
    channel?: string,			// [OPTIONAL] 
    version?: string,			// [OPTIONAL] SDK url ( whatever4 )
    unionId?: string,			// [OPTIONAL] IDid
    network?: Network,			// [OPTIONAL] 
    region?: Region,			// [OPTIONAL] 
    jsBridge?: Bridge,			// [OPTIONAL] JS bridge (Hybrid )
    // middleware
    pv?: PV,					// [OPTIONAL] PV 
    pd?: PD,                    // [OPTIONAL] PD 
    performance?: Performance,	// [OPTIONAL] 
    xhr?: Resource,				// [OPTIONAL]  ajax 
    resource?: Resource,			// [OPTIONAL] 
    event?: Event,				// [OPTIONAL] 
    log?: Log,					// [OPTIONAL] 
}



